.layoutGrid{
    height: 100vh;
    display: grid;
    grid-template-columns: 1fr 1fr 2fr 1fr;
}
.layoutNavigator{
    grid-column-start: 2;
    grid-column-end: 3;
}
.layoutContent{
    grid-column-start: 3;
    grid-column-end: 4;
}
.layoutLeftSide{
    grid-column-start:1;
    grid-column-end: 2;
    background-color: white;
}
.layoutRightSide{
    grid-column-start:4;
    grid-column-end: 5;
    background-color: white;
}